import React, { useRef, useEffect, useState } from "react";
import KGrid, { KPolygon, KPolygonGroup } from "./kcanvas";
import { GridLines, GridPoints } from "./Grid";
import { startShapes } from "./data";
// See Page 58 in Notes

const kgrid = new KGrid();

const Canvas = ({ mouseOver }) => {
  const cRef = useRef(null);

  const [canvasDims] = useState({ width: 800, height: 500 });
  const [activePoint, setActivePoint] = useState(null);
  const [gridPoints, setGridPoints] = useState([]);
  const [gridLines, setGridLines] = useState([]);
  const [polygonGroups, setPolygonGroups] = useState([]);
  const [t1PolygonGroups, setT1PolygonsGroups] = useState([]);

  const mouseMove = ({ x, y }) => {
    const { x: rX, y: rY } = cRef.current.getBoundingClientRect();
    const pt = kgrid.qTree.find(
      x - rX - canvasDims.width / 2,
      y - rY - canvasDims.height / 2
    );
    if (pt && pt !== activePoint) {
      setActivePoint(pt);
      mouseOver(pt);
    }
  };

  useEffect(() => {
    const width = cRef.current.clientWidth;
    const height = cRef.current.clientHeight;

    kgrid.intitialize(width, height);

    const svgNode = cRef.current;

    const polygons = startShapes.map(
      ({ kPoints, color }) =>
        new KPolygon(
          kPoints.map(kPoint => kgrid.createKPoint(kPoint)),
          color
        )
    );

    for (let i = 1; i < 6; i++) {
      polygons.push(polygons[2].copy().rotate((i * 60 * Math.PI) / 180));
    }

    for (let i = 1; i < 3; i++) {
      polygons.push(polygons[1].copy().rotate((i * 120 * Math.PI) / 180));
    }

    const kG1 = new KPolygonGroup(polygons, [
      kgrid.createKPoint([-2, -4, 2, 0]),
      kgrid.createKPoint([-2, 2, -4, 0]),
      kgrid.createKPoint([4, 2, 2, 0])
    ]);

    const p1 = new KPolygon(
      [
        kgrid.createKPoint([0, 1, -1, 0]),
        kgrid.createKPoint([0, 2, -2, 0]),
        kgrid.createKPoint([1, 1, 0, 2]),
        kgrid.createKPoint([2, 2, 0, 0]),
        kgrid.createKPoint([1, 1, 0, 0])
      ],
      "orange"
    );

    const p2 = new KPolygon(
      [
        kgrid.createKPoint([0, 2, -2, 0]),
        kgrid.createKPoint([2, 2, 0, 0]),
        kgrid.createKPoint([1, 1, 0, 2])
      ],
      "blue"
    );

    const t1Polygons = [
      new KPolygon(
        [
          kgrid.createKPoint([0, 0, 0, 0]),
          kgrid.createKPoint([0, 1, -1, 0]),
          kgrid.createKPoint([1, 1, 0, 0])
        ],
        kG1
      ),
      p1,
      p2
    ];

    const p1G = new KPolygonGroup(t1Polygons, [
      kgrid.createKPoint([0, 0, 0, 0]),
      kgrid.createKPoint([0, 2, -2, 0]),
      kgrid.createKPoint([2, 2, 0, 0])
    ]);

    const t1PolygonGroups = [p1G];

    for (let i = 1; i < 6; i++) {
      t1PolygonGroups.push(
        t1PolygonGroups[0].copy().rotate((i * 60 * Math.PI) / 180)
      );
    }

    // t1Polygons.push(t1Polygons[0].copy().rotate((-60 * Math.PI) / 180));

    // const kG2 = kG1
    //   .copy()
    //   .translate({ dBat: -1, dCat: 1 })
    //   .translate({ dAnt: -1, dBat: -1 });

    // kG1
    //   .rotate((60 * Math.PI) / 180)
    // .translate({ dAnt: 2, dBat: 4, dCat: -2 });
    //   .translate({ dAnt: -1, dBat: -1 });

    setPolygonGroups([kG1]);

    setT1PolygonsGroups(t1PolygonGroups);

    // kgrid.rotateKPoint(
    //   {
    //     ant: -2,
    //     bat: -1,
    //     cat: -1,
    //     dog: 0
    //   },
    //   (60 * Math.PI) / 180
    // );

    const pt1A = kgrid.createKPoint([0, 0, 0, 0]);
    const pt2A = kgrid.createKPoint([0, 1, -1, 0]);

    const lenA = Math.sqrt(
      Math.pow(pt2A.x - pt1A.x, 2) + Math.pow(pt2A.y - pt1A.y, 2)
    );

    const pt1B = kgrid.createKPoint([-2, -4, 2, 0]);
    const pt2B = kgrid.createKPoint([-2, 2, -4, 0]);

    const lenB = Math.sqrt(
      Math.pow(pt2B.x - pt1B.x, 2) + Math.pow(pt2B.y - pt1B.y, 2)
    );

    console.log("LEN", lenA, lenB, lenB / lenA);

    const pt1C = kgrid.createKPoint([-2, -4, 2, 0]);
    const pt2C = kgrid.createKPoint([0, 0, 0, 0]);

    const dx = pt2C.x - pt1C.x;
    const dy = pt2C.y - pt1C.y;

    console.log(dx, dy);

    const { pts, lines } = kgrid.getGrid();

    setGridPoints(pts);
    setGridLines(lines);

    svgNode.addEventListener("mousemove", mouseMove);

    return () => {
      svgNode.removeEventListener("mousemove", mouseMove);
    };
  }, []);

  return (
    <div>
      <svg ref={cRef} width={canvasDims.width} height={canvasDims.height}>
        <g
          transform={`translate(${canvasDims.width / 2}, ${canvasDims.height /
            2})`}
        >
          <GridLines lines={gridLines} />
          <GridPoints points={gridPoints} activePoint={activePoint} />
          {/* {polygonGroups.map((kPolygonGroup, idx) => (
            <PolygonGroup drawHull key={idx} kPolygonGroup={kPolygonGroup} />
          ))} */}
          {t1PolygonGroups.map((kPolygonGrp, idx) => (
            <PolygonGroup key={idx} kPolygonGroup={kPolygonGrp} />
          ))}
        </g>
      </svg>
    </div>
  );
};

const PolygonGroup = React.memo(({ kPolygonGroup, drawHull = false }) => {
  return (
    <g>
      {kPolygonGroup.kPolygons.map((kPolygon, idx) => (
        <Polygon key={idx} kPolygon={kPolygon} tMat={kPolygonGroup.tMat} />
      ))}
    </g>
  );
});

const Polygon = React.memo(({ kPolygon, tMat }) => {
  if (kPolygon.color instanceof KPolygonGroup) {
    const tMat = kPolygon.tMat();
    return (
      <>
        {kPolygon.color.kPolygons.map((kPolygon, idx) => (
          <Polygon key={idx} kPolygon={kPolygon} tMat={tMat} />
        ))}
      </>
    );
  } else {
    return (
      <path
        d={kPolygon.pathString(tMat)}
        fill={kPolygon.color}
        fillOpacity={0.4}
        stroke={"black"}
        strokeWidth={3}
        strokeLinejoin="round"
      />
    );
  }
});

export default Canvas;
